import { WelcomeLayout } from 'shared/layouts/WelcomeLayout';
import { useFormik } from 'formik';
import * as yup from 'yup';
import { useDispatch } from 'react-redux';
import { KntInput } from 'shared/components/Fields';
import Actions from 'store/actions';
import { AxiosError } from 'axios';
import { useHistory, useLocation } from 'react-router-dom';
import qs from 'query-string';
import { useAxios, useNotification, useNotistack } from 'shared/hooks';
import { Helmet } from 'react-helmet';
import React from 'react';
import { useParams } from 'react-router';
import { storage } from 'shared/services';
import { useAuth } from '../../modules/auth/hooks/useAuth';
import { useTranslation } from 'react-i18next';
const FormRequest = Actions.entity.formRequest;

const validationSchema = yup.object({
	username: yup.string().email('Enter a valid email').required('Email is required'),
	new_password: yup
		.string()
		.strict(true)
		.trim('Password cannot be blank')
		.min(6, 'Password should be of minimum 6 characters length')
		.required('Password is required'),
	passwordConfirmation: yup
		.string()
		.oneOf([yup.ref('new_password')], 'Passwords are not the same!')
		.required('Password confirmation is required!')
});

function SetPassword() {
	const dispatch = useDispatch();
	const history = useHistory();
	const location = useLocation();
	const params = useParams<{ code?: string }>();
	const { notifVisible, setNotifVisible, handleNotificationClose, Notification } =
		useNotification();
	const { showNotification } = useNotistack();
	const { isAuthentificated } = useAuth();
	const { t } = useTranslation();

	const handleRedirect = () => {
		if (isAuthentificated) {
			history.push('/switch-business');
			return;
		}
		history.push('/');
		window.location.reload();
	};

	const { fetchData: handleAccountActivationRequest } = useAxios({
		url: '',
		dynamicUrl: true,
		cb: {
			success: (data, args) => {
				// console.log('data,args', { data, args });
				setNotifVisible(true);
				const { setPassWordData, setSubmitting } = args[0];
				const token = setPassWordData?.token;

				setSubmitting(false);
				storage.set('token', token);
				setTimeout(() => {
					handleRedirect();
				}, 2000);
			},
			error: (error, args) => {
				showNotification({
					message: t('something_went_wrong'),
					variant: 'error'
				});
			}
		}
	});

	const query = qs.parse(location.search) || { username: '' };
	const formik = useFormik({
		initialValues: {
			username: query.username,
			new_password: '',
			passwordConfirmation: ''
		},
		validationSchema: validationSchema,
		onSubmit: (values, { setFieldError, setSubmitting }) => {
			const filteredValues = { ...values };
			// @ts-ignore
			delete filteredValues.passwordConfirmation;

			dispatch(
				FormRequest({
					entity: 'change_password',
					name: 'change_password',
					url: '/user/set-password/',
					method: 'patch',
					params: {},
					values: filteredValues,
					cb: {
						success: (data: any) => {
							if ('code' in params) {
								localStorage.clear();
								handleAccountActivationRequest({
									url: `/sts/${params.code}/`,
									setSubmitting,
									setPassWordData: data
								});
							}
						},
						error: (error: AxiosError) => {
							// setFieldError('token', error.response?.data?.message)
							if (error && error.response?.status === 400) {
								showNotification({
									message:
										'This invitation link is invalid.\n' +
										'Please contact to invited person to have a new invitation link.',
									variant: 'error'
								});
							}
							setSubmitting(false);
						},
						finally: () => {}
					}
				})
			);
		}
	});

	// @ts-ignore
	return (
		<WelcomeLayout
			title={'Set password for new user'}
			description={`
										You should set your personal password for getting started with 
										email <span class="text-primary">${query.username}</span>
									`}
			goToPageLink={'/signin'}
			goToPage={'Do you want to login to another account?'}
			goToPageLinkText={'Sign In'}
			back
			content={
				<>
					<Helmet>
						<title>KENNEKT | Set new password</title>
					</Helmet>
					<form onSubmit={formik.handleSubmit}>
						<div className=" w-full font-poppins md:w-[423px]">
							<KntInput
								name={'new_password'}
								placeholder={'Password'}
								label={'Password'}
								required
								onChange={formik.handleChange}
								type={'password'}
								value={formik.values.new_password}
								error={formik.touched.new_password && Boolean(formik.errors.new_password)}
								helperText={formik.touched.new_password && formik.errors.new_password}
							/>
							<KntInput
								name={'passwordConfirmation'}
								placeholder={'Confirm password'}
								label={'Password confirmation'}
								required
								onChange={formik.handleChange}
								type={'password'}
								value={formik.values.passwordConfirmation}
								error={
									formik.touched.passwordConfirmation && Boolean(formik.errors.passwordConfirmation)
								}
								helperText={
									formik.touched.passwordConfirmation && formik.errors.passwordConfirmation
								}
								classNames={'mb-8'}
							/>
							<Notification
								severity={'info'}
								anchorOrigin={{
									vertical: 'bottom',
									horizontal: 'center'
								}}
								handleClose={handleNotificationClose}
								open={notifVisible}
								message="Your account has been confirmed. You will redirected into the app in seconds.."
								autoHideDuration={3000}
							/>
							<button
								disabled={formik.isSubmitting}
								className={
									'bg-primary py-3.5 uppercase transition ' +
									'w-full rounded-lg text-center text-sm font-light text-white hover:bg-primary-dark'
								}
							>
								{formik.isSubmitting ? 'Setting password. Please wait a while' : 'Set password'}
							</button>
						</div>
					</form>
				</>
			}
		/>
	);
}

export default SetPassword;
